<template>
    <SignUpPage v-if="signedIn"
    @onsignup="signedIn = false"
    @guestenter="signedIn = false; loggedIn = true"
    @tologin="signedIn = false" />
  <LogOnPage
    v-else-if="!loggedIn"
    @onlogon="loggedIn = true"
    @tosignup="signedIn = true"
    @guestenter="loggedIn = true" />
  <HomePage v-else />
</template>

<script>

import HomePage from './components/HomePage.vue';
import LogOnPage from './components/LogOnPage.vue';
import SignUpPage from './components/SignUpPage.vue';

export default {
  name: 'App',
  data() {
    return {
      loggedIn: false,
      signedIn: true
    }
  },
  components: {
    HomePage,
    LogOnPage,
    SignUpPage
}
}
</script>

