<template>
    <div class="HoverImage" @mouseleave="handleMouseLeave" @mouseenter="handleMouseEnter">
        <img :src="currentImage()" />
    </div>
</template>

<script>
export default {
    name: "HoverImage",
    props: {
        default_img: {
            type: String
        },
        hover_img: {
            type: String
        }
    },
    data() {
        return {
            state: "default",
        };
    },
    methods: {
        handleMouseLeave() {
            this.state = "default";
        },
        handleMouseEnter() {
            this.state = "hover";
        },
        currentImage() {
            return require('@/assets/' + (this.state === "default" ? this.default_img : this.hover_img));
        }
    },

};
</script>


<style>
.HoverImage {
    height: 55px;
    position: relative;
    width: 55px;
}

.HoverImage img {
    left: 8px;
    position: absolute;
    top: 6px;
    width: 40px;
    border-radius: 8px;
}

.HoverImage.default {
    background-color: #ffffff;
    border-radius: 8px;
}

.HoverImage.hover {
    background-color: #d9d9d9;
    border-radius: 8px;
}
</style>
