<template>
    <div class="home-page">
        <div class="div-2">
            <img class="ep-setting" alt="Setting" src="../assets/ep_setting.svg" />
            <div class="location">
                <div class="user-home-text">Home page</div>
                <div class="line-2" />
                <div class="user-selected-folder">Folder</div>
            </div>
            <img class="home-logo" alt="Lissajous curve" src="../assets/Lissajous_curve_3by2-04 1.png" />
            <NewFolder class="newfolder" />
            <HoverImage class="folder-listview" default_img="folder_listview_selected.png"
                hover_img="folder_listview_unselected.png" />
            <HoverImage class="image-preview-selected" default_img="icons_unselected.svg" hover_img="icons_selected.svg" />
            <div class="rectangle" id="container">
                <div class="folder-contents">
                    <FolderLargeIcon @onmenu="showMenu" class="folder-item" v-for="i in 40" :key="i" />
                </div>
            </div>
        </div>
        <FolderItemPopupMenu id="folder-item-popup-menu" />
    </div>
</template>
  
<script>
import FolderItemPopupMenu from "./FolderItemPopupMenu.vue";
import NewFolder from "./NewFolder.vue";
import HoverImage from "./HoverImage.vue";
import FolderLargeIcon from "./FolderLargeIcon.vue";

export default {
    name: "HomePage",
    components: {
        NewFolder,
        HoverImage,
        FolderLargeIcon,
        FolderItemPopupMenu
    },
    data() {
        return {
            lastClicked: 0
        };
    },
    methods: {
        showMenu(e) {
            if ((Date.now() - this.lastClicked) < 11) return;
            this.lastClicked = Date.now();
            console.log(e);
            const menu = document.getElementById("folder-item-popup-menu");
            menu.style.display = "block";
            menu.style.left = e.clientX + "px";
            menu.style.top = e.clientY + "px";
            document.addEventListener("click", this.hideMenu);
        },
        hideMenu(e) {
            if ((Date.now() - this.lastClicked) < 11) return;
            this.lastClicked = Date.now();
            const menu = document.getElementById("folder-item-popup-menu");
            if (menu.contains(e.target)) return;
            menu.style.display = "none";
            document.removeEventListener("click", this.hideMenu);
        }
    },
    mounted () {
        console.log("loaded")
        const ele = document.getElementById('container');

        let pos = { top: 0, left: 0, x: 0, y: 0 };

        const mouseDownHandler = function (e) {
            ele.style.cursor = 'grabbing';
            ele.style.userSelect = 'none';

            pos = {
                left: ele.scrollLeft,
                top: ele.scrollTop,
                // Get the current mouse position
                x: e.clientX,
                y: e.clientY,
            };

            document.addEventListener('mousemove', mouseMoveHandler);
            document.addEventListener('mouseup', mouseUpHandler);
        };

        const mouseMoveHandler = function (e) {
            // How far the mouse has been moved
            const dx = e.clientX - pos.x;
            const dy = e.clientY - pos.y;

            // Scroll the element
            ele.scrollTop = pos.top - dy;
            ele.scrollLeft = pos.left - dx;
        };

        const mouseUpHandler = function () {
            ele.style.cursor = 'grab';
            ele.style.removeProperty('user-select');

            document.removeEventListener('mousemove', mouseMoveHandler);
            document.removeEventListener('mouseup', mouseUpHandler);
        };

        // Attach the handler
        ele.addEventListener('mousedown', mouseDownHandler);
    },
};
</script>
  
<style scoped>
